import React from 'react';
import './App.css';
import { Map } from 'mapbox-gl';
import mapboxgl from 'mapbox-gl';
import { getExistingLocations as getVisitedLocations, getFirstLocation, getLocations, WLocation } from './Points';
import { LocationOverview } from './locationOverview/LocationOverview';
import { ModalState, WModal } from './modal/Modal';


export default class App extends React.Component<{}, { map?: Map, newLocations: WLocation[]}> {

  render() {
    var locs = (this.state?.newLocations || []).map(i => <WModal key={Date.now()} title={i.title + " ðŸŽ‰"} modalState={ModalState.Peek}>
      <p>{i.description}</p>
      <a className="modal-link" href={i.link} target="blank">
        <p className="modal-link-container">Open secret</p>
      </a>
    </WModal>)
    return [
      <div key="a" id="map"></div>,
      <LocationOverview key="b"/>,
      locs
    ];
  }

  componentDidMount() {
    this.SetupMap();
    this.checkForNewLocations();
  }

  private SetupMap() {
    mapboxgl.accessToken = 'pk.eyJ1IjoibmV3bm90dGFrZW5uYW1lIiwiYSI6ImNrZjhuZzZvZjA2MDUyd3B4MmdkMzhpamEifQ.3p91vKWfOce6fRzddTg_qA';
    var map = new mapboxgl.Map({
      container: 'map',
      style: 'mapbox://styles/newnottakenname/ckf8o4w885kx21and6jq56h8q',
      zoom: 6,
      center: [5.55, 52.316667]
    });

    this.setState({
      map: map
    });

    var visited = getVisitedLocations();
    getLocations().forEach(i => {
      new mapboxgl.Marker({
        color: visited.some(j => i.id === j.id) ? "orange" : undefined 
      })
        .setLngLat([i.coordinates.latitude, i.coordinates.longitude])
        .addTo(map);
    });
  }

  checkForNewLocations() {
    if (!navigator.geolocation) {
      alert("Please allow me to use your location. Pls?")
    } else {
      navigator.geolocation.getCurrentPosition((position) => {
        try {
          let map = this.state.map;
          const latitude = position.coords.latitude;
          const longitude = position.coords.longitude;
          map?.setCenter([longitude, latitude]);
          map?.setZoom(12);

          var visited = getVisitedLocations();
          getLocations().forEach(i => {
            if (visited.some(j => i.id === j.id)) {
              return;
            }

            if (this.isCloseTo(i, longitude, latitude)) {
              this.addToLocalStorage(i);
              this.setState({
                newLocations: (this.state.newLocations || []).concat([i])
              })
            }
          });
        } catch(e) {
          alert("Error. App.tsx:88 " + JSON.stringify(e));
        }
      }, (error) => {
        alert("Error getting current location " + JSON.stringify(error));
      });

      navigator.geolocation.watchPosition((position) => {
          let map = this.state.map;
          const latitude = position.coords.latitude;
          const longitude = position.coords.longitude;
          map?.setCenter([longitude, latitude]);
          map?.setZoom(12);
        }, (error) => {
          alert("Error getting current location from watchPosition" + JSON.stringify(error));
      })

      if (getVisitedLocations().length === 0) {
        this.addToLocalStorage(getFirstLocation());
        this.setState({
          newLocations: (this.state.newLocations || []).concat(getFirstLocation())
        })
      }
    }
  }


  addToLocalStorage(i: WLocation) {
    window.localStorage.setItem("foundLocations", JSON.stringify(getVisitedLocations().concat([i])));
  }


  isCloseTo(i: WLocation, longitude: number, latitude: number) {
    const newLocal = this.distance(i.coordinates.longitude, i.coordinates.latitude, latitude, longitude);
    console.warn(newLocal, i, longitude, latitude);
    return newLocal < 1;
  }

  

  distance(lon1:number, lat1:number, lon2:number, lat2:number) {
    function toRad(input: number)
    {
      return input * Math.PI / 180;
    }

    var R = 6371; // Radius of the earth in km
    var dLat = toRad(lat2 - lat1);  // Javascript functions in radians
    var dLon = toRad(lon2 - lon1);
    var a = Math.sin(dLat / 2) * Math.sin(dLat / 2) +
      Math.cos(toRad(lat1)) * Math.cos(toRad(lat2)) *
      Math.sin(dLon / 2) * Math.sin(dLon / 2);
    var c = 2 * Math.atan2(Math.sqrt(a), Math.sqrt(1 - a));
    var d = R * c; // Distance in km
    return d;
  }
}