export class Coords {
    latitude!: number;
    longitude!: number;
}

export class WLocation {
    id!: string;
    title!: string;
    description!: string;
    coordinates!: Coords;
    link!: string;
}

export function getLocations(): WLocation[] {
    return [
        {
            id: "temp",
            title: "Secret location",
            description: "Here",
            coordinates: {
                longitude: 52.1,
                latitude: 4.21,
            },
            link: "https://nntn.nl"
        },
        {
            id: "temp-venlo",
            title: "Venlo",
            description: "Here",
            coordinates: {
                longitude: 51.23,
                latitude: 6.10,
            },
            link: "https://nntn.nl"
        },
        {
            id: "temp-ah",
            title: "Albert Heijn",
            description: "Its an Albert Heijn. Gotta debug somehow",
            coordinates: {
                longitude: 51.9966037, 
                latitude: 4.3508977
            },
            link: "https://nntn.nl"
        }
    ]
}

export function getExistingLocations(): WLocation[] {
    if (window.localStorage.getItem("foundLocations") === null) {
        window.localStorage.setItem("foundLocations", "[]");
    }
    var json = JSON.parse(window.localStorage.getItem("foundLocations") || "");
    // var json = getLocations();

    return (json as WLocation[]);
}

export function getFirstLocation(): WLocation {
    return {
        id: "introduction",
        coordinates: {
            latitude: 0,
            longitude: 0
        },
        description: "Welcome to your present! This website will guide you through a lot of fun memories. Go to the points on the map an have fun!",
        title: "Introduction",
        link: "https://www.youtube.com/watch?v=dQw4w9WgXcQ"
    }
}