import React from "react";
import { ModalState, WModal } from "../modal/Modal";
import { getExistingLocations, WLocation } from "../Points";
import './LocationOverview.css'

export class LocationOverview extends React.Component<{}, { location?: WLocation}> {


    render() {
        var listItems = getExistingLocations().map(i => <li key={i.id} onClick={() => {
            this.setState({
                location: i
            });}
        }>{i.title}</li>)

        return [<WModal key="a" title="Found locations" modalState={ModalState.Peek} hideClose={true}>
            <ul>
                {listItems}
            </ul>
        </WModal>,
            this.createModal(this.state?.location)
        ]
    }

    createModal(location?: WLocation) {
        if(location === undefined)
        {
            return null;
        }
        var mod = <WModal key={Date.now()} title={location.title} modalState={ModalState.Open}>
            <p>{location.description}</p>
         <a className="modal-link" href={location.link} target="blank">
           <p className="modal-link-container">Open secret</p>
         </a>
        </WModal>
        return mod;
    }
}